@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;700&display=swap);
*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
    font-family: 'Rubik', sans-serif;
    font-size: 16px;
}

* > input:focus, textarea:focus, select:focus, button:focus{
    outline-style: none;
}

.hero {
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-position: center;
}

.hero .content {
  width: 100%;
  height: 100%;
  background-color: rgba(18, 137, 165, 0.5);
  padding: 0 100px;
}

.hero .content .content-text {
  padding-top: 350px;
  width: 100%;
  text-align: center;
}

.hero .content .content-text .title {
  width: 650px;
  margin: auto;
  position: relative;
}

.hero .content .content-text .title h1 {
  font-size: 3rem;
  color: #ffffff;
  text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  line-height: 1;
  margin-bottom: 20px;
  padding-top: 20px;
}

.hero .content .content-text .title :after {
  content: "";
  background: #7499f1;
  position: absolute;
  top: 0;
  right: 45%;
  height: 5px;
  width: 73px;
  border-radius: 3px;
}

.hero .content .content-text .text {
  width: 100%;
  margin-bottom: 40px;
}

.hero .content .content-text .text p {
  color: #ffffff;
  text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  font-size: 1.3rem;
}

.hero .content .content-text .button button {
  width: 130px;
  height: 40px;
  border: none;
  font-size: 14px;
  border-radius: 20px;
  color: #ffffff;
  background: #4cd87d;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.35);
  font-weight: 400;
  transition: all .2s;
}

.hero .content .content-text .button :hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}

.hero .content .content-text .arrow {
  color: #ffffff;
  margin-top: 80px;
}

.hero .content .content-text .arrow svg {
  font-size: 2rem;
  text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  -webkit-animation-name: shake-with-delay;
          animation-name: shake-with-delay;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes shake-with-delay {
  from, 16%, to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  1.6%, 4.8%, 8%, 11.2%, 14.4% {
    -webkit-transform: translate3d(-10px, 0, 0);
    transform: translate3d(-10px, 0, 0);
  }
  3.2%, 6.4%, 9.6%, 12.8% {
    -webkit-transform: translate3d(10px, 0, 0);
    transform: translate3d(10px, 0, 0);
  }
}

@keyframes shake-with-delay {
  from, 16%, to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  1.6%, 4.8%, 8%, 11.2%, 14.4% {
    -webkit-transform: translate3d(-10px, 0, 0);
    transform: translate3d(-10px, 0, 0);
  }
  3.2%, 6.4%, 9.6%, 12.8% {
    -webkit-transform: translate3d(10px, 0, 0);
    transform: translate3d(10px, 0, 0);
  }
}

.about {
  width: 100vw;
  height: calc(100vh - 80px);
  background-image: url(/iclinic/static/media/background-about.01f8e7a5.jpg);
  background-size: cover;
  background-position: center;
  box-shadow: 0px 0px 50px 50px white inset;
  -webkit-box-shadow: 0px 0px 50px 50px white inset;
  -moz-box-shadow: 0px 0px 50px 50px white inset;
}

.about .content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.about .content .text1, .about .content .text2, .about .content .image {
  width: 50%;
  height: 50%;
  padding: 50px;
}

.about .content .text1 {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about .content .text1 .white-space {
  white-space: pre-wrap;
  text-align: right;
  font-size: 1.5rem;
  color: #6d6d6d;
  font-weight: 400;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
}

.about .content .text1 .white-space span {
  font-size: 1.5rem;
  color: #6d7ad6;
  font-weight: 600;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
  transition: all .2s;
}

.about .content .text1 .white-space :hover {
  font-size: 1.55rem;
  text-shadow: 0px 5px 5px rgba(0, 0, 0, 0.25);
}

.about .content .text2 {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about .content .text2 .white-space {
  white-space: pre-wrap;
  text-align: left;
  font-size: 1.5rem;
  color: #6d6d6d;
  font-weight: 400;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
}

.about .content .text2 .white-space span {
  font-size: 1.5rem;
  color: #fa9746;
  font-weight: 600;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
  transition: all .2s;
}

.about .content .text2 .white-space :hover {
  font-size: 1.55rem;
  text-shadow: 0px 5px 5px rgba(0, 0, 0, 0.25);
}

.about .content .image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about .content .image img {
  height: 100%;
}

.header {
  width: 100vw;
  height: 80px;
  border-radius: 0px 0px 15px 15px;
  position: fixed;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.3);
  z-index: 9999;
}

.header .content {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 80px;
}

.header .content .logo {
  height: 70%;
}

.header .content .button button {
  width: 200px;
  height: 46px;
  border: none;
  font-size: 14px;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.35);
  border-radius: 23px;
  color: #ffffff;
  background: #7499f1;
  font-weight: 400;
  transition: all .2s;
}

.header .content .button :hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}

.header .content .button .home {
  width: 100px;
  height: 46px;
  margin-left: 40px;
}

.form {
  width: 100%;
  height: 100%;
  border-radius: 0 0 15px 15px;
  padding: 40px;
}

.form .succes-message {
  width: 100%;
  text-align: center;
}

.form .succes-message p {
  width: 100%;
  font-size: 1.1rem;
  margin: 3px 0;
  color: #6d6d6d;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
}

.form .input-box {
  width: 100%;
  display: flex;
  justify-content: center;
}

.form .input-box input {
  width: 100%;
  height: 35px;
  margin-bottom: 20px;
  background: #e7f0f7;
  border: none;
  border-radius: 18px;
  padding: 0 10px;
  color: #6e6e6e;
}

.form .input-box ::-webkit-input-placeholder {
  font-size: 0.9rem;
}

.form .input-box :-ms-input-placeholder {
  font-size: 0.9rem;
}

.form .input-box ::placeholder {
  font-size: 0.9rem;
}

.form .input-box #cep {
  width: 25%;
  margin-right: 20px;
}

.form .input-box #number {
  width: 25%;
  margin-left: 20px;
}

.form .input-box #uf {
  width: 25%;
  margin-left: 20px;
}

.form .button-box {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.form .button-box button {
  width: 130px;
  height: 36px;
  border: none;
  font-size: 14px;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.35);
  border-radius: 18px;
  margin-top: 40px;
  color: #ffffff;
  background: #7499f1;
  font-weight: 400;
  transition: all .2s;
}

.form .button-box :hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}

.form .button-box .save {
  background: #4cd87d;
}

.form .button-box .cancel {
  background: #f17474;
}

.footer {
  width: 100vw;
  height: 80px;
  background-color: #7499f1;
}

.footer .content {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer .content p {
  color: #ffffff;
  font-size: 1.2rem;
}

.main {
  width: 100vw;
  height: calc(100vh - 80px);
  background: url(/iclinic/static/media/background-about.01f8e7a5.jpg);
  background-position: center;
  background-size: cover;
  padding: 110px 80px 30px 80px;
  position: relative;
}

.main .content {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.main .content .title {
  width: 100%;
  text-align: center;
  margin-bottom: 10px;
}

.main .content .title p {
  font-size: 1.5rem;
  color: #4a5878;
  font-weight: 400;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
}

.main .content .clinic {
  width: 350px;
  height: 350px;
  margin: 15px;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 20px;
  box-shadow: 0px 0px 7px 7px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 0px 0px 7px 7px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 0px 7px 7px rgba(0, 0, 0, 0.1);
  position: relative;
}

.main .content .clinic p {
  font-size: 1.1rem;
  margin: 3px 0;
  color: #6d6d6d;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
}

.main .content .clinic p span {
  font-size: 1.1rem;
  font-weight: 500;
  color: #4a5878;
}

.main .content .clinic hr {
  margin: 5px 0;
  width: 200px;
  height: 3px;
  background-color: #4a5878;
}

.main .content .clinic .position {
  position: absolute;
  bottom: 5px;
  right: 15px;
  font-size: 0.6rem;
}

.main .backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
}

.main .modal-container {
  width: 700px;
  margin: auto;
  background-color: #ffffff;
  box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
  position: fixed;
  left: 50%;
  top: 40%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 15px;
}

.main .modal-container .modal-content {
  width: 100%;
}

.main .modal-container .modal-content .header-modal {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #7499f1;
  border-radius: 15px 15px 0 0;
}

.main .modal-container .modal-content .header-modal p {
  font-size: 1.1rem;
  font-weight: 400;
  color: #ffffff;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.35);
}

.main .modal-container .modal-content .main-modal {
  width: 100%;
  height: 100%;
  border-radius: 0 0 15px 15px;
}

