.main {
  width: 100vw;
  height: calc(100vh - 80px);
  background: url(../../../Utils/Images/background-about.jpg);
  background-position: center;
  background-size: cover;
  padding: 110px 80px 30px 80px;
  position: relative;
}

.main .content {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.main .content .title {
  width: 100%;
  text-align: center;
  margin-bottom: 10px;
}

.main .content .title p {
  font-size: 1.5rem;
  color: #4a5878;
  font-weight: 400;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
}

.main .content .clinic {
  width: 350px;
  height: 350px;
  margin: 15px;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 20px;
  box-shadow: 0px 0px 7px 7px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 0px 0px 7px 7px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 0px 7px 7px rgba(0, 0, 0, 0.1);
  position: relative;
}

.main .content .clinic p {
  font-size: 1.1rem;
  margin: 3px 0;
  color: #6d6d6d;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
}

.main .content .clinic p span {
  font-size: 1.1rem;
  font-weight: 500;
  color: #4a5878;
}

.main .content .clinic hr {
  margin: 5px 0;
  width: 200px;
  height: 3px;
  background-color: #4a5878;
}

.main .content .clinic .position {
  position: absolute;
  bottom: 5px;
  right: 15px;
  font-size: 0.6rem;
}

.main .backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
}

.main .modal-container {
  width: 700px;
  margin: auto;
  background-color: #ffffff;
  box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
  position: fixed;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  border-radius: 15px;
}

.main .modal-container .modal-content {
  width: 100%;
}

.main .modal-container .modal-content .header-modal {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #7499f1;
  border-radius: 15px 15px 0 0;
}

.main .modal-container .modal-content .header-modal p {
  font-size: 1.1rem;
  font-weight: 400;
  color: #ffffff;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.35);
}

.main .modal-container .modal-content .main-modal {
  width: 100%;
  height: 100%;
  border-radius: 0 0 15px 15px;
}
