.hero {
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-position: center;
}

.hero .content {
  width: 100%;
  height: 100%;
  background-color: rgba(18, 137, 165, 0.5);
  padding: 0 100px;
}

.hero .content .content-text {
  padding-top: 350px;
  width: 100%;
  text-align: center;
}

.hero .content .content-text .title {
  width: 650px;
  margin: auto;
  position: relative;
}

.hero .content .content-text .title h1 {
  font-size: 3rem;
  color: #ffffff;
  text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  line-height: 1;
  margin-bottom: 20px;
  padding-top: 20px;
}

.hero .content .content-text .title :after {
  content: "";
  background: #7499f1;
  position: absolute;
  top: 0;
  right: 45%;
  height: 5px;
  width: 73px;
  border-radius: 3px;
}

.hero .content .content-text .text {
  width: 100%;
  margin-bottom: 40px;
}

.hero .content .content-text .text p {
  color: #ffffff;
  text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  font-size: 1.3rem;
}

.hero .content .content-text .button button {
  width: 130px;
  height: 40px;
  border: none;
  font-size: 14px;
  border-radius: 20px;
  color: #ffffff;
  background: #4cd87d;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.35);
  font-weight: 400;
  transition: all .2s;
}

.hero .content .content-text .button :hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}

.hero .content .content-text .arrow {
  color: #ffffff;
  margin-top: 80px;
}

.hero .content .content-text .arrow svg {
  font-size: 2rem;
  text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  animation-name: shake-with-delay;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

@keyframes shake-with-delay {
  from, 16%, to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  1.6%, 4.8%, 8%, 11.2%, 14.4% {
    -webkit-transform: translate3d(-10px, 0, 0);
    transform: translate3d(-10px, 0, 0);
  }
  3.2%, 6.4%, 9.6%, 12.8% {
    -webkit-transform: translate3d(10px, 0, 0);
    transform: translate3d(10px, 0, 0);
  }
}

.about {
  width: 100vw;
  height: calc(100vh - 80px);
  background-image: url(../../../Utils/Images/background-about.jpg);
  background-size: cover;
  background-position: center;
  box-shadow: 0px 0px 50px 50px white inset;
  -webkit-box-shadow: 0px 0px 50px 50px white inset;
  -moz-box-shadow: 0px 0px 50px 50px white inset;
}

.about .content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.about .content .text1, .about .content .text2, .about .content .image {
  width: 50%;
  height: 50%;
  padding: 50px;
}

.about .content .text1 {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about .content .text1 .white-space {
  white-space: pre-wrap;
  text-align: right;
  font-size: 1.5rem;
  color: #6d6d6d;
  font-weight: 400;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
}

.about .content .text1 .white-space span {
  font-size: 1.5rem;
  color: #6d7ad6;
  font-weight: 600;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
  transition: all .2s;
}

.about .content .text1 .white-space :hover {
  font-size: 1.55rem;
  text-shadow: 0px 5px 5px rgba(0, 0, 0, 0.25);
}

.about .content .text2 {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about .content .text2 .white-space {
  white-space: pre-wrap;
  text-align: left;
  font-size: 1.5rem;
  color: #6d6d6d;
  font-weight: 400;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
}

.about .content .text2 .white-space span {
  font-size: 1.5rem;
  color: #fa9746;
  font-weight: 600;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
  transition: all .2s;
}

.about .content .text2 .white-space :hover {
  font-size: 1.55rem;
  text-shadow: 0px 5px 5px rgba(0, 0, 0, 0.25);
}

.about .content .image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about .content .image img {
  height: 100%;
}
