.form {
  width: 100%;
  height: 100%;
  border-radius: 0 0 15px 15px;
  padding: 40px;
}

.form .succes-message {
  width: 100%;
  text-align: center;
}

.form .succes-message p {
  width: 100%;
  font-size: 1.1rem;
  margin: 3px 0;
  color: #6d6d6d;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
}

.form .input-box {
  width: 100%;
  display: flex;
  justify-content: center;
}

.form .input-box input {
  width: 100%;
  height: 35px;
  margin-bottom: 20px;
  background: #e7f0f7;
  border: none;
  border-radius: 18px;
  padding: 0 10px;
  color: #6e6e6e;
}

.form .input-box ::placeholder {
  font-size: 0.9rem;
}

.form .input-box #cep {
  width: 25%;
  margin-right: 20px;
}

.form .input-box #number {
  width: 25%;
  margin-left: 20px;
}

.form .input-box #uf {
  width: 25%;
  margin-left: 20px;
}

.form .button-box {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.form .button-box button {
  width: 130px;
  height: 36px;
  border: none;
  font-size: 14px;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.35);
  border-radius: 18px;
  margin-top: 40px;
  color: #ffffff;
  background: #7499f1;
  font-weight: 400;
  transition: all .2s;
}

.form .button-box :hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}

.form .button-box .save {
  background: #4cd87d;
}

.form .button-box .cancel {
  background: #f17474;
}
